// src/About.js
import React from "react";
import alienMusk from "../assets/dd.jpg";
import "./About.css";

const About = () => {
  return (
    <section id="about" className="about">
      <h2>About AELON</h2>
      <img src={alienMusk} alt="Alien Musk" className="alien-musk-img" />
      <p>
        AELON is the latest meme coin in the crypto space, aiming to bring fun
        and value to the community.
      </p>
    </section>
  );
};

export default About;
