const moveFromLeftToRight = keyframes`
  0% {
    transform: translateX(-100%);
  }
  100% {
    transform: translateX(100%);
  }
`;

const StyledTokenomics = styled.div`
  text-align: center;
  color: #9bafc1;
  padding: 50px;

  > *:not(h1) {
    // transform: skewY(-3deg);
    margin-top: 20px;
  }

  h1 {
    margin-bottom: 20px;
    color: white;
  }

  h2 {
    color: white;
  }

  span {
    color: red;
  }

  @media (max-width: 640px) {
    padding: 20px;
  }
`;

const ChartContainer = styled.div`
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  flex-wrap: wrap;
`;

const LabelItem = styled.div`
  display: flex;
  align-items: center;
  gap: 10px;
  margin-top: 5px;

  @media (max-width: 640px) {
    flex-direction: column;
    align-items: flex-start;
  }
`;

const CurvedText = styled.h2`
  font-size: 5rem;
  animation: ${moveFromLeftToRight} 60s linear infinite;

  @media (max-width: 640px) {
    font-size: 3rem;
  }
`;

const ChartLabel = styled.div`
  display: flex;
  flex-direction: column;
  align-items: flex-start;
`;

export default Tokenomics;
